  header {
    box-sizing: border-box;
    margin: 0 auto;
    padding: 8px;
    background: linear-gradient(to left top, blue, red);
    color: #ffffff;

    h1 {
      font-size: 30px;
    }

    h2 {
      font-size: 25px;
    }

    p {
      font-weight: 700;
      font-size: 20px;
    }


    @media (min-width:300px) {
      font-weight: 200;
    }

    @media (min-width:600px) {
      font-weight: 200;
    }

    @media (max-width:500px) {
      font-size: 30px;
    }

    @media (max-width:700px) {
      font-size: 40px;
    }
  }


  main {
    width: 1000px;
    margin: 0 auto;
  }

  pre,
  code {
    width: 100%;
    white-space: pre-line;
  }

  footer {
    font-size: 20px;
    text-align: center;

    @media (max-width:700px) {
      font-size: 30px;
    }

    @media (max-width:500px) {
      font-size: 40px;
    }

  }

  .e {
    opacity: 0;
  }

  .e-v {
    animation: fadeIn 1s ease forwards;
  }

  @keyframes fadeIn {
    0% {
      opacity: 0;
      transform: translateY(20px);
    }

    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }

  .space {
    width: 100%;
    height: 100vh;
    background: #f0f0f0;
  }

  .container {
    display: flex;
    justify-content: space-evenly;
  }
